@import "./mixins";

// COLORS
:root {
  --color-eastbay: #484283;
  --color-main: #444b58;
  --color-main-hover: #31343c;
  --color-black: #000;
  --color-white: #fff;
  --color-accent: #f14f4f;
  --color-accent-hover: #cc4949;
  --color-border: rgba(255, 255, 255, 0.5);
  --color-bg: #fff4ee;
  --color-cameo: #dbbba9;
  --color-gray: #b2b5bb;
  --color-jumbo: #808080;
  --color-btn-active: #8c2828;
  
  //отступы 
  --grid-gap: 20px;
  --main-offset: 40px;
  --small-offset: 10px;
  --second-offset: 20px;
  --big-offset: 60px;

  //шрифты
  --font-family: "Intro", sans-serif;
  --middle-high-bold:  700 30px / 140% var(--font-family);
  --prehigh-book:  400 24px / 24px var(--font-family);
  --prehigh-regular:  600 20px / 20px var(--font-family);
  --middle-book:  500 16px / 140% var(--font-family);
  --middle-regular:  600 16px / 140% var(--font-family);
  --small-book:  400 14px / 140% var(--font-family);
}

@include mobile {
  :root {
  --grid-gap: 10px;
  --main-offset: 20px;
  --small-offset: 14px;
  --second-offset: 8px;
  --big-offset: 30px;
  }
}
